import React from 'react';
// import { Link } from 'react-router-dom';
// import styled from 'styled-components'
import Link from  './style.js'
import {NavbarList,Logo,UlList,LogoText,ListItems,Anchor} from './style.js'
const Navbar = ()=> {
  return (
    <NavbarList>
            
            <div className="container">
                
                <Logo className="logo">
                    <LogoText className="logo-text">Ultra Profile</LogoText>
                </Logo>
                
                <UlList>
                    <ListItems><Link to='/'> Home</Link></ListItems>
                    <ListItems><Anchor>Work</Anchor></ListItems>
                    <ListItems><Anchor>Portfolio</Anchor></ListItems>
                    <ListItems><Anchor>Resume</Anchor></ListItems>
                    <ListItems><Anchor>About</Anchor></ListItems>
                    <ListItems><Link to="/contact">Contact</Link></ListItems>
                </UlList>  
            </div>  
    </NavbarList>
  )
}

export default Navbar;