import React, { useState, useEffect } from 'react';
import axios from 'axios'
import {ProfileSkills,ProfileDiv,SkillsTitleSpan,ProfileTitleSpan,SkillTitle,ProfileTitle,BarParent,BarParentSpan,BarTitle,BarPerc,SkillsDesc,Skills,Bar,ProfileList,ProfileItem,ProfileSpan} from "./style.js"

const Profile = () => {
  const [BarItem, setBar] = useState([]);
  useEffect(() => {
    axios.get('js/data.json').then(res =>{setBar(res.data.profile)})
  },[]) 
  const bars = BarItem.map((bar) => {
    return (
      <Bar key ={bar.id}>
      <BarTitle>{bar.title} </BarTitle>
      <BarPerc>{bar.parcentage}</BarPerc>
      <BarParent>
          <BarParentSpan className={bar.sp}></BarParentSpan>
      </BarParent>
    </Bar>
  )
})
  return (
  
    <ProfileSkills>
            <div className="container">
                <ProfileDiv>
                    <ProfileTitle><ProfileTitleSpan>My </ProfileTitleSpan>Profile</ProfileTitle>
                    <ProfileList>
                        <ProfileItem >
                            <ProfileSpan>Name</ProfileSpan>
                            AbdElhak Mehda
                        </ProfileItem>
                        <ProfileItem >
                            <ProfileSpan>Birthday</ProfileSpan>
                            02/01/1991
                        </ProfileItem>
                        <ProfileItem>
                            <ProfileSpan>Address</ProfileSpan>
                            EL oued - MiniHouse
                        </ProfileItem>
                        <ProfileItem >
                            <ProfileSpan>Phone</ProfileSpan>
                            +213664726837
                        </ProfileItem>
                        <ProfileItem >
                            <ProfileSpan>Email</ProfileSpan>
                            ae.mehda@gmail.com
                        </ProfileItem>
                        <ProfileItem >
                            <ProfileSpan>Website</ProfileSpan>
                            <ProfileSpan web>www.notyet.com</ProfileSpan>
                        </ProfileItem>
                    </ProfileList>
                </ProfileDiv>
                
                <Skills>
                    <SkillTitle>Some <SkillsTitleSpan>skills</SkillsTitleSpan></SkillTitle>
                    <SkillsDesc>
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quos praesentium blanditiis esse cupiditate, omnis similique.
                    </SkillsDesc>
                    {/* <Bar>
                        <BarTitle>Bootstrap</BarTitle>
                        <BarPerc>100%</BarPerc>
                        <BarParent>
                            <BarParentSpan sp1></BarParentSpan>
                        </BarParent>
                    </Bar>
                    
                    <Bar>
                        <BarTitle>CSS3</BarTitle>
                        <BarPerc>90%</BarPerc>
                        <BarParent>
                            <BarParentSpan sp2></BarParentSpan>
                        </BarParent>
                    </Bar>
                    
                    <Bar>
                        <BarTitle>Photoshop</BarTitle>
                        <BarPerc>80%</BarPerc>
                        <BarParent>
                            <BarParentSpan sp3></BarParentSpan>
                        </BarParent>
                    </Bar> */}
                {bars}
                </Skills>
                
            </div>
        </ProfileSkills>
    
  )
}

export default Profile;