import React, { useState, useEffect } from 'react';
import axios from 'axios'
import {SocialSection,SpanInfo,SocialMedia,SocialIcon,SocialP,Span} from './style.js'

const SocialMeadia = () => {
  const [Social, setSocial] = useState([])
  useEffect(() => {
    axios.get('js/data.json').then(res=>{setSocial(res.data.social)})
  }, [])
  const SocialItems = Social.map((socialitem) => {
    return (
      <SocialMedia item ={socialitem.id} key={socialitem.id}>
        <SocialIcon  className={socialitem.icon}></SocialIcon>
      <SocialP>
          <Span> {socialitem.title} </Span>
          <SpanInfo>{socialitem.body}</SpanInfo>
      </SocialP>
  </SocialMedia>
    )
  })
  return (
    <SocialSection>
            
    {SocialItems}           

   
</SocialSection>
  )
}

export default SocialMeadia;