import React,{Component} from 'react';
import axios from 'axios'
import {Work,WorkTitle,WorkSpan,Part,Icon,PartTitle,Line,PartDesc } from  './style.js'

class Works extends Component  {
  state = {
    works:[]
  }
  componentDidMount() {
    axios.get('js/data.json').then(res => this.setState({
      works: res.data.works
    }))
  }
  render() {
    const { works } = this.state;
    const worksList = works.map((workItem) => {
      return (
        <Part first ={workItem.id} key ={workItem.id}>
        <Icon className={workItem.icon_name}></Icon>
          <PartTitle >{workItem.title}</PartTitle>
        <Line />
        <PartDesc>
        {workItem.body}
          </PartDesc>
    </Part>
    
      )
    })
     return (
    <Work>
            <div className="container">
                <WorkTitle><WorkSpan>My</WorkSpan> Work</WorkTitle>
               {worksList}
                                   
            </div>
        </Work>
  )
  }
 
}

export default Works;